/* google font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

/* layout */


:root {
  --primary: rgba(43, 63, 176, 0.745);
  --error: red;
}

body {
  background: #f1f1f1;
  margin: 0;
  font-family: "Poppins";
}

header {
  background: salmon;
  max-width: 1920px;
  margin: 0;
  padding: 10px 30px;
  display: flex;
  align-items: auto;
}

header .container {
  max-width: 1920px;
  margin: 0;
  padding: 10px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.title {
  margin-left: 80px;
  color: #fff;
  text-decoration: none;
}

.title:hover {
  margin-left: 80px;
  color: #fff;
  text-decoration: none;
}

header a   {
  margin-right: 80px;;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
}


.loginLanding {
  margin-left:350px;
  margin-top: 300px;
}

.userNav  {
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  border: 3px solid white;
  border-radius: 10px;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
  
}


.userNav:hover {
  margin-right: 80px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #ffa07a;
  border: 3px solid white;
  border-radius: 10px;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
}

.pages {
  max-width: 1400px;
  padding: 20px;
  margin: 0 auto;
}


.balance-form {
text-align: center;
  border: 3px solid salmon;
  border-radius: 20px;
  margin-top: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding: 20px 20px;
  max-width: 300px;
}

.balance-form label{
  color: white;
  background-color: black;
  border-radius: 20px;
  max-width: 200px;
}

.home {
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 100px;
}
.deposit-details {
  background: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.05);
}
.deposit-details h4 {
  margin: 0 0 10px 0;
  font-size: 1.2em;
  color: var(--primary);
}
.deposit-details p {
  margin: 0;
  font-size: 0.9em;
  color: #555;
}
.deposit-details span {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  background: #f1f1f1;
  padding: 6px;
  border-radius: 50%;
  color: #333;
}

/* withdrawal details */

.withdrawal-details {
  background: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.05);
}
.withdrawal-details h4 {
  margin: 0 0 10px 0;
  font-size: 1.2em;
  color: var(--primary);
}
.withdrawal-details p {
  margin: 0;
  font-size: 0.9em;
  color: #555;
}
.withdrawal-details span {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  background: #f1f1f1;
  padding: 6px;
  border-radius: 50%;
  color: #333;
}

/* new workout form */

label, input {
  display: block;
}

input {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

form button { 
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 10px;
  font-weight: "Poppins" ;  
  border-radius: 4px;
  cursor: pointer;
}

div.error {
  padding: 10px;
  background: #ffefef;
  border: 1px solid var(--error);
  color: var(--error);
  border-radius: 4px;
  margin-top: 20px 0;
}

input.error {
  border: 1px solid var(--error)
}


/*  navbar */

nav { 
  
    display: flex;
    align-items: right;

}

nav a {
  margin-left: 10px;

}

nav button {
  background: #fff;
  color: var(--primary);
  border: 2px solid var(--primary);
  padding: 6px 10px;
  border-radius: 4px;
  font-family: "Poppins";
  cursor: pointer;
  font-size: 1em;
  margin: 5px;
  
}

/* auth forms*/

form.signup, form.login {
  max-width: 400px;
  margin: 40px auto;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
}